  display: block;
}
.modal__error {
  display: block;
  padding-bottom: 50px;
  animation-name: shake;
  animation-duration: 0.5s;
}
.modal--js {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation-name: bounce;
  animation-duration: 0.5s;
}
