@keyframes bounce {
  0% {
    transform: translateY(-1000%) translateX(-50%);
  }
  70% {
    transform: translateY(-50%) translateX(-50%);
  }
  90% {
    transform: translateY(-45%) translateX(-50%);
  }
  100% {
    transform: translateY(-50%) translateX(-50%);
  }
}
@keyframes shake {
  0% {
    transform: translateY(-1000%) translateX(-50%);
  }
  70% {
    transform: translateY(-50%) translateX(-50%);
  }
  90% {
    transform: translateY(-45%) translateX(-50%);
  }
  100% {
    transform: translateY(-50%) translateX(-50%);
  }
}
